.logo {
  width: 240px;
  height: 80px; }
  .logo__name {
    fill: #f9fafa;
    -webkit-transform: translate(80px, 0px) scale(2.5);
        -ms-transform: translate(80px, 0px) scale(2.5);
            transform: translate(80px, 0px) scale(2.5); }
  .logo__slogan {
    fill: #f9fafa;
    -webkit-transform: translate(85px, 70px) scale(0.3);
        -ms-transform: translate(85px, 70px) scale(0.3);
            transform: translate(85px, 70px) scale(0.3); }
  .logo__icon {
    fill: #ffac00;
    -webkit-transform: translate(0, 0) scale(0.8);
        -ms-transform: translate(0, 0) scale(0.8);
            transform: translate(0, 0) scale(0.8); }



